*{
  margin: 0;
}

body {
  -webkit-user-select:none;
  -moz-user-select:none;
  -ms-user-select:none;
  user-select:none
}




.title{
  margin: 50px 0px 50px 30px;
}

.FashList{
  height: 200px;
  width: 500px;
  border: 1px solid black;
  position: relative;

  margin: 0px 0px 10px 30px;
}

.FashList > h2{
  padding: 20px 0px 0px 20px;
}

.FashList > span{
  position: absolute;
  font-size: 20px;
  
  right: 0px;
  top: 30px;
  right: 30px;
}

.FashList > button{
  position: absolute;
  
  left: 20px;
  bottom: 20px;
}


/* modal */
.modal{
  height: 200px;
  margin-top: 20px;
  
  background-color: lightgray;
}


/* input */
.input{
  margin-left: 30px;
}

.input > button{
  margin-left: 20px;
}